<template>
  <div class="ContentLoaderLayerGradient" :style="{
    ['--layerColor']: color,
    ['--nodeSize']: `${size}em`,
  }">
    <span
      class="ball"
      v-for="(node, index) in nodes"
      :key="index"
      :style="{
        animationDuration: duration,
        animationDelay: node.delay,
        opacity: node.opacity,
      }"
    >
    </span>
  </div>
</template>
<script>
export default {
  name: 'ContentLoaderLayerGradient',
  props: {
    color: {
    },
    delay: {
      type: Number,
      default: 0,
    },
    tailLength: {
      type: Number,
      default: 1,
    },
    size: {
      type: Number,
      default: 1,
    },
  },
  data() {
    const duration = 1.2;
    const nodeAmount = 30 * this.tailLength;
    const tail = 0.2 * this.tailLength;
    const nodes = [];

    for (let i = 0; i < nodeAmount; i += 1) {
      nodes.push({
        delay: `${(i / nodeAmount) * tail + this.delay}s`,
      });
    }

    return {
      duration: `${duration}s`,
      nodes,
    };
  },
};

</script>
<style lang="scss" scoped>
  $outer-color: #bd9562;
  $center-color: #f8e9aa;

  @mixin bottom {
    transform: translate(0.5em, 0.89em);
    background-color: $outer-color;
  }

  @keyframes loading-animation {
    0% { // Top
      transform: translate(0px, 0px);
      background-color: $outer-color;
    }

    12% {
      @include bottom;
    }

    25% { // Top
      transform: translate(1em, 0em);
      background-color: $outer-color;
    }

    37% {
      transform: translate(0.6em, 0em);
      background-color: $center-color;
    }

    50% {
      transform: translate(0.3em, 0.534em);
      background-color: $center-color;
    }

    62% {
      @include bottom;
    }

    75% {
      transform: translate(0.70em, 0.534em);
      background-color: $center-color;
    }

    87% {
      transform: translate(0.4em, 0em);
      background-color: $center-color;
    }

    100% {
      transform: translate(0em, 0em);
      background-color: $outer-color;
    }
  }

  .ContentLoaderLayerGradient {
    position: absolute;
    width: 1em;
    height: 0.89em;

    .ball {
      position: absolute;
      display: block;
      width: var(--nodeSize, 0.1em);
      height: var(--nodeSize, 0.1em);
      left: calc(var(--nodeSize, 0.1em) / -2);
      top: calc(var(--nodeSize, 0.1em) / -2);
      border-radius: 50%;
      background-color: var(--layerColor, #e2d170);

      animation-name: loading-animation;
      animation-duration: 1s;
      animation-iteration-count: infinite;
      animation-fill-mode: both;
      animation-timing-function: ease-in-out;
      animation-timing-function: linear;
    }
  }
</style>
