<template>
    <div class="AccountForms">
        <div class="panels">
            <AddressPanel
                v-for="(address, index) in account.addresses"
                class="address-panel"
                :key="index"
                :address="address"
                :editable="isPanelEditable(index)"
                :title="`Adres ${index + 1}`"
                @edit="$emit('editAddress', index)"
                @save="$emit('saveChanges')"
                />
            <PasswordPanel
                :editable="passwordEditable"
                @edit="$emit('editPassword')"
                @save="$emit('savePassword', $event)"
                />
            <DeleteAccountPanel
                v-if="isAllowedToDelete"
                :account="account"
                @delete="$emit('delete')"
                />
        </div>
    </div>
</template>

<script>
    import { faTrash } from '@fortawesome/free-solid-svg-icons/faTrash';
    import { library } from '@fortawesome/fontawesome-svg-core';
    import AddressPanel from './AddressPanel.vue';
    import PasswordPanel from './PasswordPanel.vue';
    import DeleteAccountPanel from './DeleteAccountPanel.vue';
    import { getAuthService } from '../../services/authService';
    import { ADMIN } from '../../types/role.types';


    library.add(faTrash);

    export default {
        name: 'AccountForms',
        components: {
            AddressPanel,
            PasswordPanel,
            DeleteAccountPanel,
        },
        props: {
            account: {
                required: true,
            },
            addressesEditModes: {
                required: true,
            },
            passwordEditable: {
                type: Boolean,
                required: true,
            },
        },
        data() {
            return {
                confirmDeletePopup: false,
                userRole: null,
            };
        },
        computed: {
            isAllowedToDelete() {
                return this.userRole === ADMIN;
            },
        },
        methods: {
            isPanelEditable(index) {
                return !!this.addressesEditModes[index];
            },
        },
        async mounted() {
            this.userRole = await getAuthService().getRole();
        },
    };
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";
    @import "@/assets/scss/globals.scss";

    .AccountForms {
        box-sizing: border-box;
        width: 100%;
        max-width: 900px;
        margin: 0 auto;
        margin-top: 16px;
        
        .panels {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: auto auto;
            grid-gap: 50px 200px;
            margin: 0 auto;

            .addresses {
                display: flex;

                .address-panel {
                    width: 300px;
                }
            }
        }
    }

    @media only screen and (max-width: $phone-max-width) {
        .AccountForms {
            max-width: 500px;

            .panels {
                display: grid;
                grid-template-columns: 1fr;
                grid-template-rows: auto auto;
                grid-gap: 50px 200px;
            }
        }
    }
</style>
