<template>
    <ActionPanel class="EditablePanel" :title="title">
        <template v-slot:actions>
            <EditButton
                :editable="editable"
                @click="$emit('edit')"
                :editLabel="editLabel"
                :cancelLabel="cancelLabel"
                />
        </template>
        <form @submit.prevent="$emit('save')">
            <div class="content">
                <slot></slot>
            </div>
            <SmallButton
                v-if="editable"
                type="submit"
                class="save-button"
                border-color="primary"
                >
                Zapisz
            </SmallButton>
        </form>
    </ActionPanel>
</template>

<script>
    import { library } from '@fortawesome/fontawesome-svg-core';
    import { faPen, faTimes } from '@fortawesome/free-solid-svg-icons';
    import SmallButton from '@/components/input/SmallButton.vue';
    import EditButton from '@/components/input/EditButton.vue';
    import ActionPanel from './ActionPanel.vue';

    library.add(faPen, faTimes);

    export default {
        name: 'EditablePanel',
        components: {
            ActionPanel,
            SmallButton,
            EditButton,
        },
        props: {
            title: {
                type: String,
                required: true,
            },
            editLabel: {
                type: String,
                default: 'Edytuj',
            },
            cancelLabel: {
                type: String,
                default: 'Anuluj',
            },
            editable: {
                type: Boolean,
                required: true,
            },
        },
    };
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";
    @import "@/assets/scss/globals.scss";

    .content {
        margin-bottom: 20px;
