<template>
  <div class="ContentLoaderLayer" :style="{
    ['--layerColor']: color,
    ['--nodeSize']: `${size}em`,
  }">
    <span
      class="ball"
      v-for="(node, index) in nodes"
      :key="index"
      :style="{
        animationDuration: duration,
        animationDelay: node.delay,
        opacity: node.opacity,
      }"
    >
    </span>
  </div>
</template>
<script>
export default {
  name: 'ContentLoaderLayer',
  props: {
    color: {
    },
    delay: {
      type: Number,
      default: 0,
    },
    tailLength: {
      type: Number,
      default: 1,
    },
    size: {
      type: Number,
      default: 1,
    },
  },
  data() {
    const duration = 1.2;
    const nodeAmount = 30 * this.tailLength;
    const tail = 0.2 * this.tailLength;
    const nodes = [];

    for (let i = 0; i < nodeAmount; i += 1) {
      nodes.push({
        delay: `${(i / nodeAmount) * tail + this.delay}s`,
      });
    }

    return {
      duration: `${duration}s`,
      nodes,
    };
  },
};

</script>
<style lang="scss" scoped>
  $loader-width: 1;
  $loader-height: 1;

  @keyframes loading-animation {
    0% {
      transform: translate(0px, 0px);
    }

    12% {
      transform: translate(#{$loader-width * 0.5em}, #{$loader-height * 0.89em});
    }

    25% {
      transform: translate(#{$loader-width * 1em}, #{$loader-height * 0em});
    }

    37% {
      transform: translate(#{$loader-width * 0.6em}, #{$loader-height * 0em});
    }

    50% {
      transform: translate(#{$loader-width * 0.3em}, #{$loader-height * 0.534em});
    }

    62% {
      transform: translate(#{$loader-width * 0.5em}, #{$loader-height * 0.89em});
    }

    75% {
      transform: translate(#{$loader-width * 0.70em}, #{$loader-height * 0.534em});
    }

    87% {
      transform: translate(#{$loader-width * 0.4em}, #{$loader-height * 0em});
    }

    100% {
      transform: translate(#{$loader-width * 0em}, #{$loader-height * 0em});
    }
  }

  .ContentLoaderLayer {
    position: absolute;
    width: #{$loader-width * 1em};
    height: #{$loader-height * 0.89em};

    .ball {
      position: absolute;
      display: block;
      width: var(--nodeSize, 0.1em);
      height: var(--nodeSize, 0.1em);
      left: calc(var(--nodeSize, 0.1em) / -2);
      top: calc(var(--nodeSize, 0.1em) / -2);
      border-radius: 50%;
      background-color: var(--layerColor, #e2d170);

      animation-name: loading-animation;
      animation-duration: 1s;
      animation-iteration-count: infinite;
      animation-fill-mode: both;
      animation-timing-function: ease-in-out;
      animation-timing-function: linear;
    }
  }
</style>
